.details-restaurant__flex-container {
    @media screen and (min-width: $tablet) {
        display: flex;
        flex-direction: row-reverse;
    }
}


.details-restaurant__flex--desktop-margin {
    @media screen and (min-width: $desktop) {
        margin-left: 17px;
        // flex-grow: 2;
        max-width: 942px;
    }
}

.details-restaurant {
    margin: 0 auto;
    max-width: 414px;
    padding: 30px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-image: linear-gradient(to right, rgba(var(--primary-dark-rgb), 0.65), rgba(var(--primary-dark-rgb), 0.65)),
        url(../images/details-restaurant-bg-mobile.jpg);



    @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
        & {
            background-image: linear-gradient(to right, rgba(var(--primary-dark-rgb), 0.65),
                    rgba(var(--primary-dark-rgb), 0.65)), url(../images/details-restaurant-bg-mobile@2x.jpg);
        }
    }

    @media screen and (min-width: $tablet) {
        max-width: 1024px;
        padding-left: 50px;
        padding-right: 50px;

        & {
            background-image: linear-gradient(to right, rgba(var(--primary-dark-rgb), 0.65),
                    rgba(var(--primary-dark-rgb), 0.65)), url(../images/details-restaurant-bg-tablet.jpg);
        }


        @media screen and (min-device-pixel-ratio: 2),
        screen and (min-resolution: 192dpi),
        screen and (min-resolution: 2dppx) {
            & {
                background-image: linear-gradient(to right, rgba(var(--primary-dark-rgb), 0.65),
                        rgba(var(--primary-dark-rgb), 0.65)), url(../images/details-restaurant-bg-tablet@2x.jpg);
            }
        }

    }

    @media screen and (min-width: $desktop) {
        max-width: 1280px;

        & {
            background-image: linear-gradient(to right, rgba(var(--primary-dark-rgb), 0.65),
                    rgba(var(--primary-dark-rgb), 0.65)), url(../images/details-restaurant-bg-desktop.jpg);
        }


        @media screen and (min-device-pixel-ratio: 2),
        screen and (min-resolution: 192dpi),
        screen and (min-resolution: 2dppx) {
            & {
                background-image: linear-gradient(to right, rgba(var(--primary-dark-rgb), 0.65),
                        rgba(var(--primary-dark-rgb), 0.65)), url(../images/details-restaurant-bg-desktop@2x.jpg);

            }
        }

    }
}

.details-restaurant__title-box {
    margin-left: 80px;
    margin-bottom: 48px;

    @media screen and (min-width: $tablet) {
        margin-left: 0;

    }

}



.details-title {
    font-weight: 700;
    font-size: 24px;
    line-height: 1.21;
    letter-spacing: 0.02em;
    color: var(--primary-light);
    margin-bottom: 20px;



}

.details-restaurant__descr {
    font-weight: 600;
    font-size: 12px;
    line-height: 1.25;
    letter-spacing: 0.02em;
    color: var(--bg-image-secondary);

    @media screen and (min-width: $tablet) {
        font-size: 16px;
        line-height: 1.25;
        width: 545px;
        // margin-right: 130px;
    }


    @media screen and (min-width: $desktop) {
        max-width: 600px;
    }
}


.details-buttons {

    text-align: center;
    // margin-top: 32px;

    @media screen and (min-width: $tablet) {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        margin-bottom: 70px;

    }

    // @media screen and (min-width: $desktop) {
    //     max-width: 338px;
    // }
}

.details-button {
    @include button-transparent();
    //  cursor: pointer;
    transition: background-color var(--time) var(--cubic);

    &:not(:last-child) {
        margin-bottom: 16px;
    }

    &:hover,
    &:focus,
    &:active {
        background: var(--primary-light);
        border: 2px solid var(--secondary);
        border-radius: 25px;
    }
}





