}

.plug-jpg {
    padding: 0;
    display: flex;
    list-style: none;
}

.plug-jpg__item {
    @media screen and (min-width: $desktop) {


        &:nth-child(1) {
            margin-left: 15px;
        }

        &:not(:last-child) {
            margin-right: 87px;
        }

    }
}

.plug__details-hotel {
    margin-right: 16px;
}

.details-hotel__plug-list {
    // margin: 0;
    padding: 0;
    display: flex;
    list-style: none;
}

.details-hotel__plug-item {
    @media screen and (min-width: $desktop) {

        &:nth-child(3) {
